<template>
	<div>
  <div class="page-title">
    <h3>{{'profilePageName' | localizeFilter}}</h3>
  </div>

  <form class="form" @submit.prevent="onSubmit">
    <div class="input-field">
      <input
          id="description"
          type="text"
          v-model="userName"
          :class="{invalid: $v.userName.$dirty && !$v.userName.required}"
      >
      <label class="active" for="description">{{'labelName' | localizeFilter}}</label>
      <span
            class="helper-text invalid"
            v-if="$v.userName.$dirty && !$v.userName.required">`Введите ${}имя пользователя`</span>
    </div>
    <div class="switch">
    <label>
      En
      <input type="checkbox" v-model="locale"
         true-value="ru-RU"
          false-value="en-US"
          @change="change">
      <span class="lever"></span>
      Ru
    </label>
  </div>
    <button class="btn waves-effect waves-light" type="submit">
      {{'profileSubmitButton' | localizeFilter}}
      <i class="material-icons right">send</i>
    </button>
  </form>
</div>
</template>

<script>
  import {required} from 'vuelidate/lib/validators'
  import localizeFilter from '@/filters/localize.filter'

  export default{
    metaInfo(){
      return {
        title: this.$title('profilePageTitle')
      }
    },
    data(){
      return {
        userName: '',
        locale: ''
      }
    },

    async mounted(){
      await this.$store.dispatch('fetchInfo')
      // const user = this.$store.getters.info
      
      this.userName = this.$store.getters.info.name
      this.locale = this.$store.getters.info.locale
    },
    validations: {
      userName: {required}
    },
    methods: {
      onSubmit(){
        if(this.$v.$invalid){
          this.$v.$touch()
          return
        }
        console.log(this.locale)
        this.$store.dispatch('updateInfo', {name: this.userName, locale: this.locale})
      },
      change(){
        console.log(this.locale)
      }
    }
  }
</script>
<style scoped>
  .switch{
    margin-bottom: 30px;
  }
</style>